<template>
  <el-card class="right-sidebar-container">
    <div
      v-if="$store.state.user"
    >
      <!-- style="width:100%;display:flex;justify-content:center;align-items:center;flex-wrap:wrap" -->
      <!-- 我的文件 -->
      <button class="myFile_btn">
        <div
          class="title unSelect_text"
          @click="$router.push('/fileList')"
        >
          我的文件
        </div>
      </button>
      <!-- 我的分享 -->
      <button
        class="myFile_btn"
        @click="$router.push('/share')"
      >
        <div
          class="title unSelect_text"
        >
          我的分享
        </div>
      </button>
      <!-- 下载历史 -->
      <button
        class="myFile_btn"
      >
        <div
          class="title unSelect_text"
          @click="$router.push('/history')"
        >
          下载历史
        </div>
      </button>
      <!-- 测试 -->
      <button
        class="myFile_btn"
        @click="$router.push('/test')"
      >
        <div
          class="title unSelect_text"
        >
          测试
        </div>
      </button>
    </div>
    <div v-else>
      <el-card class="hello">
        欢迎加入我们！！
        <p>介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍。</p>
      </el-card>
    </div>
    <el-card class="user_container">
      <!-- 用户菜单 -->
      <div
        v-if="showUserMenu"
        class="logout showAnimate"
      >
        <el-button
          class="userBtn"
          @click="logout"
        >
          帮助
        </el-button>
        <el-button
          class="userBtn"
          @click="logout"
        >
          关于
        </el-button>
        <el-button
          class="userBtn"
          @click="logout"
        >
          登出
        </el-button>
      </div>

      <div class="userInfo">
        <img
          class="userAvatar"
          :src="userAvatar"
          alt=""
        >
        <div
          class="userName"
          @click="showUserInfo"
        >
          {{
            $store.state.user?$store.state.user.name:'未登录'
          }}
        </div>
        <div
          v-if="$store.state.user"
          class="rest"
        >
          <span style="font-size:10px">
            {{ used + ' ' + '/ ' + userInfo.capacity/(1024*1024*1024) + 'GB' }}
          </span>
          <el-progress
            :text-inside="true"
            :stroke-width="5"
            :percentage="percentage"
            style="box-sizing:border-box;margin:0px 2px;margin-right:5px"
          />
        </div>
        <div
          v-if="$store.state.user"
          class="menu"
          @click="(showUserMenu = !showUserMenu)"
        >
          <i class="el-icon-more" />
        </div>
      </div>
    </el-card>
  </el-card>
</template>

<script>
import {
  getUserInfo
} from "@/api/user"
import pubsub, {
  PubSub
} from "pubsub-js"
export default {
  name: "RightSidebar",
  data() {
    return {
      userAvatar: this.$store.state.user ? this.$store.state.user.avatar : require("@/assets/img/defaultAvatar.png"),
      showUserMenu: false,
      userInfo: {
        email: "",
        avatar: "",
        capacity: "",
        used: ""
      },
      percentage: 0
    }
  },
  computed: {
    used() {
      if((this.userInfo.used / (1024 * 1024)).toFixed(2)<0.01) {
        return (this.userInfo.used / (1024)).toFixed(2) + 'KB'
      } else if((this.userInfo.used / (1024 * 1024 * 1024)).toFixed(2)<0.01) {
        return (this.userInfo.used / (1024 * 1024)).toFixed(2) + 'MB'
      }
      return (this.userInfo.used / (1024 * 1024 * 1024)).toFixed(2)+'GB'
    }
  },
  async mounted() {
    PubSub.subscribe("updateUserInfo", ()=>{
      this.getUserInfo()
    })
    // PubSub.subscribe("clearUserInfo", ()=>{
    //   this.userInfo = {
    //     email: "",
    //     avatar: "",
    //     capacity: "",
    //     used: ""
    //   }
    //   this.userAvatar = require("@/assets/img/defaultAvatar.png")
    // })
    if(this.$store.state.user){
      await this.getUserInfo()
    }
    if(this.userInfo&&this.userInfo.used&&this.userInfo.capacity){
      this.percentage = Math.floor((this.userInfo.used / this.userInfo.capacity) * 100)
    }
  },

  methods: {
    loginShow() {
      pubsub.publish("login")
    },
    logout() {
      this.$store.commit("removeUser")
      this.showUserMenu = false
      this.$router.replace("/home")
      this.userInfo = {
        email: "",
        avatar: "",
        capacity: "",
        used: ""
      }
      this.userAvatar = require("@/assets/img/defaultAvatar.png")
    },
    showUserInfo() {
      if(this.$store.state.user && this.$store.state.user.token) {
        this.$router.push("/userInfo")
      } else {
        this.loginShow()
      }
    },
    async getUserInfo() {
      const {
        data
      } = await getUserInfo(this.$store.state.user.id)
      if(data.msg === '无效的token'){
        this.$message.warning('token过期，请重新登录')
        this.$store.commit('removeUser')
        this.loginShow()
        this.$router.go(0)
      } else {
        this.userInfo = data.data
        console.log(111, this.userInfo)
        this.userAvatar = this.userInfo.avatar
      }
    }
  }
}
</script>

<style lang="less" scoped>
.hello{
  width: 98%;
  height:70vh;
  background: rgba(255, 253, 253, 0.7);
  border-radius: 25px;
  font-size: 25px;
  text-align: center;
  font-family: "仓耳渔阳体 W04";
  p{
    font-size: 20px;
  }
}
.showAnimate{
    animation: fadeIn;
    animation-duration: 2s;
}
.outAnimate{
    animation: fadeOut;
    animation-duration: 0.5s;
}
.right-sidebar-container{
    margin-left: 1vw;
    width: 20vw;
    height: 98vh;
    box-sizing: border-box;
    border-radius:25px;
    border: 0;
    background: rgba(255,255,255,0.5);
    .myFile_btn{
      margin-top: 20px;
      padding:20px 0 20px 0;
      width:98%;
      border: 1px solid #e3e3df;
      border-radius: 25px;
      background: rgba(255, 253, 253, 0.7);
      cursor: pointer;
      box-sizing: border-box;
      .title{
        padding-right: 20px;
        font-size: 20px;
        text-align: center;
        font-family: "仓耳渔阳体 W04";
        box-sizing: border-box;
        color: #000000;
      }
    }
    .myFile_btn:hover{
      box-shadow: none;
      background: rgba(255, 253, 253, 0.5);
      color: #000000;
    }
    .user_container{
        width:18%;
        border-radius: 25px;
        position: fixed;
        bottom: 2.5vh;
        left: 2vw;

        .logout{
            padding: 0;
            width: 100px;
            height: 150px;
            position: fixed;
            bottom: 10vh;
            left: 15vw;
            display: flex;
            justify-content: center;
            align-content: center;
            flex-direction: column;
            flex-wrap: wrap;
            border-radius: 10px;
            overflow: hidden;
            .userBtn{
                margin: 0;
                width:inherit;
                height: 50px;
                border: 0;
                box-shadow: none;
                border-radius: 0;
                background: rgba(255, 253, 253, 0.7);
            }
        }
        .userInfo{
            display: flex;
            justify-content: flex-start;
            align-items: center;

            .userAvatar{
                width: 2vw;
                height: 2vw;
                border-radius: 50%;
                cursor: pointer;
            }
            .userName{
                padding-top: 8px;
                margin-left: 15px;
                width: 4vw;
                font-size: 16px;
                font-family: "仓耳渔阳体 W04";
                cursor: pointer;
            }
            .rest{
                flex: 1;
                text-align: center;
            }
            .menu{
                font-size: 20px;
                cursor: pointer;
            }
        }

    }
}
</style>
